@import url(<link href="https://fonts.googleapis.com/css?family=Roboto&display=swap" rel="stylesheet">);
*{
  box-sizing: border-box;
  font-family:  'Roboto', 'sans-serif';

}
html {
  font-size: 20px;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: rgb(255, 255, 255);
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden
}


#root{
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 969px 700px 900px 250px;
  grid-template-areas: "introduction introduction"
                       "technologies technologies"
                       "about about"
                       "foot foot"
}

.title{
  margin: 60px;
}





.menu{
    position: fixed;
    height: 100vh;
    width: 200px;
    background-color: rgb(31, 30, 30);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    -webkit-transition: width 1s;
    transition: width 1s;
    -webkit-transition: width 2s;
    font-size: 1.3rem;
    z-index: 2;
}

.icons{
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}
.icons a {
    color:inherit;
  }
.navlist{
    margin: 0;
    list-style: none;
}

.navlist li{
    margin-bottom: 100px;
}

.navlist li a{
    text-decoration: none;
    color: inherit;
    cursor: pointer;
}

.logo{
    text-align: center;
    width: 100%;
}

.logo::before{
    content: '';
    display: block;
    width: 100%;
    height: 8px;
    background:rgb(255, 255, 255);
}

.logo::after{
    content: '';
    display: block;
    width: 100%;
    height: 8px;
    background:rgb(255, 255, 255); 
}

.burguer{
    display: none;
    flex-direction: column;
    width: 60px;
    height: 45px;
}

.burguer-lines{
    width: 100%;
    height: 15px;
    border: 1px solid black;
    background:rgb(255, 255, 255);
}

.hidden-menu{
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    width: 100vw;
    height: 100vh;
    font-size: 1.3rem;
    z-index: 3;
    background-color:rgba(31, 30, 30,0.9);
}

.hide-nav{
    list-style: none;
}

.hide-nav li{
    margin: 80px;
}

.hide-nav li a {
    color: inherit;
    text-decoration: none;
}
.hidden-icons{
    width: 100%;
   display: flex;
   justify-content: center;
}
.hidden-icons a{
    color:inherit;
    margin-left: 30px;
}

.hidden-button{
    position: absolute;
    left: 0;
    top: 0;
    color: rgb(255, 255, 255);
    background-color: rgb(202, 44, 44);
    border: none;
    border-radius: 100px;
    width: 80px;
    padding: 15px;
}

@media (max-width:1024px){
    .menu{
        height: 20vh;
        width: 100%;
        background-color: rgb(31, 30, 30);
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }

    .icons{
        justify-content: flex-end;  
    }

    .icons a{
        margin-left: 20px;
    }

    .navlist li{
        margin-left: 40px;
        display: inline;
    }
    .logo::before{
      display: none;
    }
    
    .logo::after{
        display: none;
    }
    
}

@media screen and (max-width:800px){
    .menu{
        background-color: rgb(31, 30, 30);
        justify-content:space-evenly;
    }
    .logo{
        width: 20%;
    }

    .icons,.navlist{
        display: none;
    }
    .burguer{
        display: flex;
        margin-right: 10px;
    }
}

.introduction{
    grid-area: introduction;

    background-image: url(/website/static/media/planet.a7639264.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
   

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.profile{
    border: 5px solid rgb(243, 235, 235);
}

@media (max-width: 800px){
    .introSubtitle{
        font-size: 1.3em;
    }
}
.about{
    grid-area: about;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black; 
    background-image: -webkit-gradient(linear, left top, left bottom, from(#f5f7fa), to(#c3cfe2)); 
    background-image: -webkit-linear-gradient(#f5f7fa, #c3cfe2); 
    background-image: linear-gradient(#f5f7fa, #c3cfe2);
}

.aboutWrapper{
    width: 70%;
    display: flex;
}

.booksImg{
    width: 50%;
    height: 500px;
    box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
    margin-right: 100px;
}


.aboutCards{
    display: flex;
}

.myCard{
    font-size: 0.9rem;
    margin-right: 8px;
    border-radius: 5px;
}

@media (max-width:1400px){
    .booksImg{
        display: none;
    }
    .aboutCards{
        display: flex;
        flex-direction: column;
        
    }
}

@media (max-width:1024px){
    .aboutWrapper{
        width: 100%;
    }
}


.technologies{
    grid-area: technologies;
    display:flex;
    flex-direction: column;
    align-items: center;
    background-color:#ffff;  
    font-size: 1.5rem;
    color: black;
}

.wrapTec{
    list-style: none;
    font-size: 1.2rem;
    margin: 0;
}
.wrapTec li{
    padding: 13px;
}

.containerBar{
    display: block;
    width: 600px;
    border: 2px solid black;
    border-radius: 7px;
    height: 8px;
}

.htmlLevel{
    display: block;
    background-color: rgb(47, 47, 255);
    box-shadow:
        0 0 10px 2px #fff,  /* inner white */
        0 0  10px 2px rgb(47, 47, 255), /* middle magenta */
        0 0  10px 2px #0ff;
    height: 100%;
    width: 90%;
    -webkit-animation: level 1s forwards linear;
            animation: level 1s forwards linear;
}

.cssLevel{
    display: block;
    background-color: rgb(47, 47, 255);
    box-shadow:
        0 0 10px 2px #fff,  /* inner white */
        0 0  10px 2px rgb(47, 47, 255), /* middle magenta */
        0 0  10px 2px #0ff;
